.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Amatic SC', cursive;
  font-size: calc(20px + 2vmin);
  color: white;
}

.container {
  background-color: #282c340c;
  display: grid;
  grid-template-columns: 78% 22%;
  grid-template-rows: auto;
  grid-template-areas:     
    "colum colum colum cart"
}

.overlined {
  text-decoration:line-through;
}
.red {
  color: red;
  text-decoration: none;
}


/* CART */

.cart {
  grid-area: 'cart';
  text-align: right;
  margin-right: 2em;
  font-size: 22px;
}

.cartImg {
  width: 15%;
}

.heartImg {
  width: 10%;
}

.cartHeader {
  font-size: 2em;
  margin-bottom: 4px;
}

.inCart {
  font-family: 'Shadows Into Light Two', cursive;
  font-size: 18px;
  letter-spacing: 2px;
}

.hr {
  margin-right: 0;
  border-color: black;
  width: 170px;
}

.hr2 {
  margin-right: 0;
  border-color: black;
  width: 80%;
}

.largeFont {
  font-family: 'Shadows Into Light Two', cursive;
  font-size: 20px;
  letter-spacing: 1px;
}

.cart-p {
  margin: 0px 0px 7px 0px;
}

.slash {
  font-style: normal;
  font-size: 25px;
}

.increase, .decrease {
  font-size: 25px;
  color:#5c6068;
}

.increase:hover, .decrease:hover {
  color: black;
  cursor: default;
}

.inputDiv {
  font-family: 'Shadows Into Light Two';
}

.subscriber {
  margin-bottom: 5px;
  font-family: 'Amatic SC', cursive;
}

.subscriberInput {
  box-sizing: border-box;
  border-color: #84868b;
  font-family: 'Shadows Into Light Two', cursive;
  font-size: 17px;
  padding: 3px;
  margin: 1px;
  letter-spacing: 1px;
}

.emptyBtn {
  background-color: #a55151;
  color: white;
  font-family: 'Shadows Into Light Two', cursive;
  letter-spacing: 1px;
  padding: 2px 13px 2px 13px;
  font-size: 15px;
  margin: 7px 3px;
}

.confirmBtn {
  background-color: #4c773b;
  color: white;
  font-family: 'Shadows Into Light Two', cursive;
  letter-spacing: 1px;
  padding: 2px 13px 2px 13px;
  font-size: 15px;
  margin: 7px 3px;
}

.orderBtn {
  background-color: #3c4149;
  color: white;
  font-family: 'Shadows Into Light Two', cursive;
  letter-spacing: 1px;
  padding: 2px 13px 2px 13px;
  font-size: 15px;
  margin: 7px 3px;
}


/* PRODUCTS */

.App-column {
  grid-area: 'colum';
  background-color: #282c34;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-family: 'Amatic SC', cursive;
  color: rgb(255, 255, 255);
}

.productsHeader {
  font-size: 5em;
  margin-top: 60px;
  margin-bottom: 10px;
}

.carousel-item {
  display: inline-block;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
}

.container1 {
  display: flex;
  justify-content: center;
}

.rows {
  background-color: rgba(255, 205, 205, 0.726);
  display: flex;
  justify-content: center;
  padding: 1em;
}

.row1 {
  background-color: #e0e0e0;
  margin: 3%;
  text-align: center;
  font-family: 'Shadows Into Light Two', cursive;
  font-size: 22px;
  padding: 1em;
}

.productImg {
  height: 14em;
}

.smallText {
  font-size: 13px;
}

.row2 {
  text-align: left;
  font-family: 'Shadows Into Light Two', cursive;
  letter-spacing: 4px;
  font-size: 22px;
  padding: 1em;
}

.addToCart {
  width: 10%;
  border-color: black;
  font-family: 'Shadows Into Light Two', cursive;
  padding: 2px 3px 2px 13px;
  font-size: 22px;
  margin: 7px 0;
}

.addProductBtn {
  background-color: #3c4149;
  color: white;
  font-family: 'Shadows Into Light Two', cursive;
  letter-spacing: 1px;
  padding: 2px 13px 2px 13px;
  font-size: 22px;
  margin: 7px 3px;
}


/* CAROUSEL */

.carousel {
  overflow: hidden;
  width: 100%;
}

.inner {
  white-space: nowrap;
  transition: transform 1s;
}

.indicators {
  display: flex;
  justify-content: center;
}

.indicators > button {
  background-color: #282c34;
  color: rgb(255, 255, 255);
  border: none;
}

.arrow {
  font-size: 60px;
  margin: 10px 5px 20px 5px;
  display: inline-block;
}
